
section {
  width: 650px;
  height: 500px;
  background-color: beige;
  float: left;

  h2 {
    text-align: center;
    margin: 10px 0;
  }

  article {
    width: 270px;
    height: 190px;
    border: 1px dotted #333;
    /*점선*/
    float: left;
    padding: 10px;
    margin-left: 20px;
    margin-bottom: 10px;
    background-color: #fff;

    h3 {
      margin-bottom: 10px;
    }

    p {
      font-size: 13px;
    }
  }
}